.wrapper {
    display: flex;
    justify-content: space-between;
    gap: 24px;
    width: 100%;
    background-color: white;
    box-shadow: -1px 1px 10px 2px rgba(123, 123, 123, 0.1);
    border-radius: 12px;
    padding: 24px;
}

.tour-list {
    min-width: 400px;
    height: 100%;
    border: 1px solid #d9d9d9;
    border-radius: 12px;
    padding: 24px;

    .title {
        font-size: 18px;
        font-weight: 500;
        color: #000;
    }

    .add-tour-btn {
        width: 100%;
        margin-top: 24px;
    }
}

.tour-detail {
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 45px;

    .tour-info {
        width: 100%;
        height: 100%;
        border-radius: 12px;

        .info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 18px;

            h2 {
                color: black;
                font-weight: 600;
            }
        }

        img {
            width: 100%;
            height: 275px;
            object-fit: cover;
            border-radius: 12px;
        }

        .meta {
            display: flex;
            flex-direction: column;
            margin-top: 18px;
            padding-bottom: 24px;
            gap: 16px;
            border-bottom: 1px solid #d9d9d9;

            .meta-item {
                display: flex;
                gap: 24px;
                align-items: flex-start;
                font-size: 17px;
            }

            .label {
                min-width: 130px;
                color: var(--primary);
                font-weight: 500;
                text-align: left;
                display: inline-block;
            }

            .value {
                color: #222;
                font-weight: 400;
                text-align: left;
                display: inline-block;
            }
        }

        .about {
            padding: 12px 0;

            h3 {
                color: black;
                font-size: 16px;
                margin-bottom: 12px;
                font-weight: 500;
            }
        }
    }

    .tour-plan {
        width: 350px;
        height: 100%;
        border-radius: 12px;

        .tour-plan-title {
            padding-left: 50px;
            font-size: 18px;
            font-weight: 600;
            color: black;
            margin-bottom: 32px;
        }

        .day-item {
            display: flex;
            align-items: flex-start;
            position: relative;
            padding-bottom: 52px;
            min-height: 80px;
            padding-left: 18px;

            // Vertical dashed line
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: -54px;
                width: 0;
                height: 100%;
                border-left: 2px solid var(--primary);
                z-index: 0;
            }
            .day-number {
                position: absolute;
                left: -8px;
                top: 12px;
                transform: translateY(-50%);
                width: 18px;
                height: 18px;
                background-color: var(--primary);
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
                z-index: 1;
                border: 2px solid #fff;
            }

            .day-content {
                margin-left: 32px;

                h3 {
                    font-size: 16px;
                    font-weight: 600;
                    margin: 0 0 8px;
                    color: black;
                }

                p {
                    font-size: 14px;
                    margin: 0;
                    line-height: 1.7;
                    color: black;
                }
            }
        }
    }
}

// Add Tour
.timeRow {
    display: flex;
    gap: 24px;

    > div {
        flex: 1;
    }
}

.add-tour-plan {
    height: 300px;
    width: 100%;
}

.edit-btn,
.add-btn {
    display: flex;
    justify-content: center;
    align-items: center;
}
.pagination {
    display: flex;
    justify-content: flex-end;
    gap: 6px;
    margin-top: 12px;

    .page-btn {
        padding: 4px 8px;
        border: 1px solid #ccc;
        background: white;
        cursor: pointer;
        border-radius: 4px;

        &.active {
            background: var(--primary);
            color: white;
            border-color: var(--primary);
        }
    }

    .next,
    .prev {
        background-color: transparent;
        cursor: pointer;
    }
}
