.save-container {
    padding: 20px 32px;
    background-color: white;
    border-radius: 8px;
    box-shadow: -1px 1px 10px 2px rgba(123, 123, 123, 0.1);
    display: flex;
    justify-content: space-between;

    .title {
        font-size: 24px;
        color: var(--title-color);
    }
}