.container {
    margin: 0 auto;
    transform: translateY(-150px);
    max-width: 400px;
    padding: 32px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 0px 20px 3px rgba(123, 123, 123, 0.25);
}

.form {
    display: flex;
    flex-direction: column;
}

.title {
    color: var(--title-color);
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 32px;
}

.socialLogin {
    display: flex;
    justify-content: center;
    gap: 16px;
    margin-bottom: 24px;
}

.socialBtn {
    border: 1px solid #d9d9d9;
    background-color: transparent;
    padding: 9px 23px;
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        scale: 1.08;
    }

    img {
        width: 24px;
        height: 24px;
    }
}

.separator {
    text-align: center;
    margin: 16px 0;
    font-size: 14px;
}

.input {
    width: 100%;
    padding: 12px;
    margin-bottom: 16px;
    border: 1px solid #d9d9d9;
    border-radius: 8px;
    font-size: 14px;

    &::placeholder {
        color: var(--text-color);
    }
}
.input:focus {
    border-color: #86b7fe;
    box-shadow: 0 0 0 3px rgba(13, 110, 253, 0.2);
}
.agreeTerm {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    font-size: 14px;

    input {
        margin-right: 8px;
    }
    a {
        color: var(--header-color);
        font-weight: 600;
        text-decoration: none;
    }
}

.signUpBtn {
    width: 100%;
    background-color: var(--primary);
    color: white;
    padding: 12px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.signinText {
    text-align: start;
    margin-top: 20px;
    font-size: 14px;

    a {
        color: var(--header-color);
        font-weight: 600;
        text-decoration: none;
    }
}

.error {
    color: red;
    padding-bottom: 12px;
}
