//Budget box
.budget-box {
    min-width: 80px;
    border: 2px solid #ccc;
    border-radius: 10px;
    padding: 6px 16px;
    font-weight: 500;
    color: var(--title-color);
}

.confirm-btn {
    width: 100%;
    padding: 6px 0;
    margin: 8px 0;
    background-color: var(--primary);
    font-weight: 500;
}
